




























import { Vue } from "vue-property-decorator";
import { Role } from "../helpers/role";
import User from "../models/user";

export default Vue.extend({
    name: "Home",
    computed: {
        user(): User {
            return this.$store.state.auth.user;
        },
        isAdmin(): boolean {
            return this.user && this.user.role === Role.Admin;
        },
    },
});
